<template>
  <div>
    <span>msqqq777</span>
    <div id="myPDF" style="height: 95%; width: 95%; margin: auto;"></div>
  </div>
</template>

<script>
/*eslint-disable*/
export default {
  name: 'pdf',
  data(){
    return {

