  },
  created(){},
  mounted(){
    window.$("#myPDF").pdf({
      source: 'https://qinghuansmile.top/testCode/demo.pdf',
      loaded: this.loaded,
    });
  },
  methods:{
    loaded(){
      var $targetObj = window.$('#myPDF');
      window.cat.touchjs.init($targetObj, function (left, top, scale, rotate) {
        $targetObj.css({
          left: left,
          top: top,
          'transform': 'scale(' + scale + ') rotate(' + rotate + 'deg)',
          '-webkit-transform': 'scale(' + scale + ') rotate(' + rotate + 'deg)'
        });
      });
      // //初始化拖动手势
      window.cat.touchjs.drag(window.$('canvas'), function (left, top) {
      });
      // 初始化缩放手势
      // window.cat.touchjs.scale($targetObj, function (scale) {
      // });
      window.cat.touchjs.scale(window.$('canvas'), function (scale) {
      });
    }
  }
}
</script>
